import React from 'react';

import { imageIndicator, selected } from './imageIndicator.module.scss';

type Props = {
  total: number;
  index: number;
  className?: string;
  onClick: (i: number) => void;
};

const ImageIndicator: React.FC<Props> = ({
  total,
  index,
  className,
  onClick,
}) => {
  if (!(total > 1)) return null;

  return (
    <ul className={`${imageIndicator} ${className}`}>
      {Array.from(Array(total)).map((_, i) => (
        <li
          className={index === i ? selected : ''}
          key={i}
          onClick={() => onClick(i)}
        >
          {i + 1}
        </li>
      ))}
    </ul>
  );
};

export default ImageIndicator;
