import React, { useState, useRef, useEffect } from 'react';
import { Gestures } from 'react-gesture-handler';

import ImageIndicator from './imageIndicator';
import {
  DIRECTION_DOWN,
  DIRECTION_HORIZONTAL,
  DIRECTION_LEFT,
  DIRECTION_NONE,
  DIRECTION_RIGHT,
  DIRECTION_VERTICAL,
  INPUT_END,
} from 'hammerjs';

import ArrowLeft from '../assets/arrow-left.svg';
import ArrowRight from '../assets/arrow-right.svg';
import Close from '../assets/close.svg';
import Image from './image';

import classNames from 'classnames/bind';
import * as styles from './carouselZoom.module.scss';

const cx = classNames.bind(styles);

const THRESHOLD = 10;
const VELOCITY = 0.3;

type Props = {
  images?: GatsbyTypes.ProjectQuery.strapiProject.images;
  className?: string;
};

interface TapEvent extends HammerInput {
  tapCount: number;
}

const CarouselZoom: React.FC<Props> = ({ images, className }) => {
  if (!images || !(images.length > 0)) return null;

  const carouselElement = useRef<HTMLDivElement>();
  const [touchEvent, setEvent] = useState<HammerInput>();
  const [imageIndex, _setImageIndex] = useState(0);
  const [direction, setDirection] = useState<number>(DIRECTION_NONE);
  const [initialScrollY, setInitialScrollY] = useState<number>(0);
  const [isFullscreen, setFullscreen] = useState<boolean>(false);
  const [eventType, setEventType] = useState<string>(null);
  const [imageScale, setImageScale] = useState<number>(1);
  const [imageTransform, setImageTransform] = useState<{
    deltaX: number;
    deltaY: number;
  }>({ deltaX: 0, deltaY: 0 });
  const imagesCount = images.length;

  const getCurrentImage = (): HTMLElement => {
    if (!carouselElement.current) return;
    if (!carouselElement.current.children[imageIndex]) return;

    const imageElement =
      carouselElement.current.children[imageIndex].firstChild;
    if (!(imageElement instanceof HTMLElement)) return null;

    return imageElement;
  };

  const finishEvent = () => {
    setEvent(null);
    setEventType(null);
    setImageScale(1);
    setImageTransform({ deltaX: 0, deltaY: 0 });
    const imageElement = getCurrentImage();

    imageElement.style.transform = 'none';
  };

  const setOffsetOnElement = (newOffset: string) => {
    carouselElement.current.style.setProperty('--i', newOffset);
  };

  const setImageIndex = (newImageIndex: number) => {
    const normalizedIndex = Math.min(
      Math.max(Math.round(newImageIndex), 0),
      imagesCount - 1
    );

    _setImageIndex(normalizedIndex);
    setOffsetOnElement(normalizedIndex.toString());
    finishEvent();
  };

  const handleNext = () => setImageIndex(imageIndex + 1);
  const handlePrev = () => setImageIndex(imageIndex - 1);

  // Pan Event
  const calculateDragOffset = (deltaX: number, offsetWidth: number): number => {
    return imageIndex - deltaX / offsetWidth;
  };

  const dragCarousel = (deltaX: number) => {
    const { offsetWidth } = carouselElement.current;
    const offset = calculateDragOffset(deltaX, offsetWidth);
    setOffsetOnElement(offset.toFixed(2));
  };

  const scrollPage = (deltaY: number) => {
    window.scroll({
      top: initialScrollY - deltaY,
    });
  };

  const handleFirst = (offsetDirection: number) => {
    if (
      DIRECTION_LEFT === offsetDirection ||
      DIRECTION_RIGHT === offsetDirection
    ) {
      setDirection(DIRECTION_HORIZONTAL);
    } else {
      setDirection(DIRECTION_VERTICAL);
    }
    setInitialScrollY(window.scrollY);
  };

  const handleFinalPan = (deltaX: number, velocityX: number) => {
    finishEvent();
    const { offsetWidth } = carouselElement.current;

    if (Math.abs(deltaX) < THRESHOLD)
      return setImageIndex(calculateDragOffset(deltaX, offsetWidth));
    if (velocityX < -VELOCITY) return handleNext();
    if (velocityX > VELOCITY) return handlePrev();

    setImageIndex(calculateDragOffset(deltaX, offsetWidth));
  };

  const handlePanEvent = (event: HammerInput) => {
    const {
      velocityX,
      isFinal,
      deltaX,
      offsetDirection,
      deltaY,
      pointerType,
      velocityY,
      eventType,
    } = event;

    if (pointerType !== 'touch') {
      finishEvent();
      return;
    }

    if (direction === DIRECTION_NONE) return handleFirst(offsetDirection);

    if (isFinal || eventType === INPUT_END) {
      if (direction === DIRECTION_VERTICAL) {
        if (!isFullscreen) scrollPage(deltaY);
        else if (
          offsetDirection === DIRECTION_DOWN &&
          velocityY > VELOCITY &&
          deltaY > THRESHOLD
        ) {
          setFullscreen(false);
        }
      }
      return handleFinalPan(deltaX, velocityX);
    }

    if (direction === DIRECTION_VERTICAL) return scrollPage(deltaY);

    dragCarousel(deltaX);
  };

  // pinch
  const [imageDimensions, _setImageDimensions] = useState<{
    x: number;
    y: number;
  }>();

  const setImageDimensions = () => {
    const imageElement = getCurrentImage();
    _setImageDimensions({
      x: imageElement.offsetWidth,
      y: imageElement.offsetHeight,
    });
  };

  const handleFinishPinch = () => {
    finishEvent();
    _setImageDimensions(null);
    getCurrentImage().style.transform = 'none';
  };

  const savePinchDimensions = (
    _scale: number,
    _deltaX: number,
    _deltaY: number
  ) => {
    const scale = Math.max(_scale * imageScale, 1);
    const deltaX = _deltaX / scale + imageTransform.deltaX;
    const deltaY = _deltaY / scale + imageTransform.deltaY;

    if (scale === 1) return handleFinishPinch();

    setImageScale(scale);
    setImageTransform({ deltaX, deltaY });
  };

  const scaleImage = (_scale: number, _deltaX: number, _deltaY: number) => {
    const scale = Math.max(_scale * imageScale, 1);

    const deltaX = _deltaX / scale + imageTransform.deltaX;
    const deltaY = _deltaY / scale + imageTransform.deltaY;
    const imageElement = getCurrentImage();

    const transformScale = `scale(${scale}, ${scale})`;
    const transformTranslate = `translate(${deltaX}px, ${deltaY}px)`;

    imageElement.style.transform = `${transformScale} ${transformTranslate}`;
  };

  const moveImage = (_deltaX: number, _deltaY: number) => {
    const deltaX = _deltaX / imageScale + imageTransform.deltaX;
    const deltaY = _deltaY / imageScale + imageTransform.deltaY;

    const imageElement = getCurrentImage();
    const transformScale = `scale(${imageScale}, ${imageScale})`;
    const transformTranslate = `translate(${deltaX}px, ${deltaY}px)`;

    imageElement.style.transform = `${transformScale} ${transformTranslate}`;
  };

  const handlePinch = (event: HammerInput) => {
    if (!event) return;
    if (!isFullscreen) return handleFinishPinch();

    const {
      scale,
      eventType,
      deltaX,
      deltaY,
      type,
      velocity,
      offsetDirection,
      velocityY,
    } = event;

    if (!imageDimensions) return setImageDimensions();

    if (
      eventType === INPUT_END &&
      Math.abs(velocity) >= VELOCITY &&
      Math.abs(deltaX) > THRESHOLD
    )
      return handlePanEvent(event);

    if (
      eventType === INPUT_END &&
      offsetDirection === DIRECTION_DOWN &&
      velocityY > VELOCITY &&
      deltaY > THRESHOLD
    ) {
      setFullscreen(false);
    }

    if (eventType === INPUT_END)
      return savePinchDimensions(scale, deltaX, deltaY);

    if (type === 'pinch') scaleImage(scale, deltaX, deltaY);
    else moveImage(deltaX, deltaY);
  };

  const handleEvent = (event: HammerInput): void => {
    if (!eventType) setEventType(event.type);

    if (eventType === 'pan') return handlePanEvent(event);
    if (eventType === 'pinch') return handlePinch(event);
  };

  useEffect(() => {
    if (isFullscreen) return;

    handleFinishPinch();
  }, [isFullscreen]);

  useEffect(() => {
    if (!touchEvent) return;

    handleEvent(touchEvent);
  }, [touchEvent]);

  const handleTap = (event: TapEvent) => {
    const { tapCount } = event;

    if (!isFullscreen && tapCount === 1) setFullscreen(true);
    if (tapCount === 2) setFullscreen(false);
  };

  return (
    <div className={cx('carousel', className, { fullscreen: isFullscreen })}>
      {isFullscreen && (
        <div
          className={cx('fullscreenToggle')}
          onClick={() => setFullscreen(!isFullscreen)}
        >
          <Close />
        </div>
      )}
      <div className={cx('carouselContainer')}>
        {imageIndex > 0 && (
          <div className={cx('left')} onClick={handlePrev}>
            <ArrowLeft />
          </div>
        )}
        {imageIndex < images.length - 1 && (
          <div className={cx('right')} onClick={handleNext}>
            <ArrowRight />
          </div>
        )}
        <Gestures
          recognizers={{
            Pan: {
              events: {
                pan: setEvent,
              },
            },
            Pinch: {
              events: {
                pinch: setEvent,
              },
            },
            Tap: {
              events: {
                tap: handleTap,
              },
            },
          }}
        >
          <div className={cx('sliderContainer')} ref={carouselElement}>
            {images.map(({ attributes: image }) => (
              <div
                key={image.hash}
                className={cx('imageContainer', 'gatsbyImage')}
                style={{
                  transition: touchEvent ? 'none' : 'all 0.3s ease-in-out',
                }}
              >
                <Image className={cx('gatsbyImage')} image={image} />
              </div>
            ))}
          </div>
        </Gestures>
      </div>
      <div className={cx('imageIndicatorWrapper')}>
        <ImageIndicator
          className={cx('imageIndicator')}
          total={images.length}
          index={imageIndex}
          onClick={setImageIndex}
        />
      </div>
    </div>
  );
};

export default CarouselZoom;
